.preview-cv {
  background-color: #f8f7ff;
  width: fit-content;
  padding: 30px;
  margin: 0 auto;
  border-radius: 15px;
  color: #091103;
  margin-top: 20px;
}

.preview-cv > p {
  margin: 15px 0;
}

.contacts > p {
  margin: 0;
}

.contacts {
  margin-bottom: 10px;
}

.genInfo > p {
  margin: 0;
}

.name {
  font-size: 1.5rem;
  font-weight: 500;
}

.educ-title,
.exp-title {
  font-size: 1.3rem;
  font-weight: 500;
}

.educBG,
.expBG {
  padding-left: 18px;
}

.educBG > p {
  margin: 0;
}

.education-item,
.exp-item {
  margin-bottom: 10px;
}

.education-item > p {
  margin: 0;
}

.exp-item > p {
  margin: 0;
}

.expBG > p {
  margin: 0;
}

.school-dates,
.company-dates {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}

.school-dates > p {
  margin: 0;
}

.company-dates > p {
  margin: 0;
}

hr {
  height: 2px;
  background-color: #091103;
  border: none;
}
